.score {
	min-height: 40px;
	display: flex;
	align-items: center;
	justify-content: center;
}

@media only screen and (max-width: 700px) {
	.score {
		min-height: 20px;
	}
}

.confirmed {
	background-color: gold;
}
.finished {
	background-color: #43853d;
}
.live {
	background-color: rgb(73, 73, 255);
}
.registered {
	background-color: turquoise;
}
.scheduled {
	background-color: turquoise;
}
.pending {
	background-color: grey;
}
.aborted {
	background-color: indianred;
}

.pairingRound.roundSelected {
	background-color: cyan;
}

a.pairingRound.nullRound, a.pairingRound.nullRound:hover > * {
	background-color: lightgrey;
	color: grey;
	cursor: default;
}

td.inner {
	padding: 0;
}

.subheader {
	display: grid;
	grid-template-columns: 45% 2fr 3fr;
	column-gap: 5%;
	margin: 20px 0;
}

@media screen and (max-width: 700px) {
	.subheader {
		grid-template-columns: unset;
		grid-auto-flow: row;
		row-gap: 20px;
	}
}

.subheader > div {
	display: flex;
	width: 100%;
	border-radius: 10px;
}

.subheader > div:first-child {
	align-items: center;
	justify-content: flex-start;
}

.subheader > div:last-child {
	justify-content: flex-end;
}

.searchBar {
	display: flex;
	flex-direction: row;
	background-color: skyblue;
	align-items: center;
	align-content: center;
	box-sizing: border-box;
	border-radius: 10px;
	width: 100%;
}

.searchBar > * {
	height: 100%;
	padding: 0 5px;
}

.searchBar * {
	background-color: transparent;
}

.searchBar form {
	padding: 2px 0;
}

.searchBar div, .searchBar form, .searchBar input {
	box-sizing: border-box;
	height: 100%;
	width: 100%;
}

.searchBar input, .searchBar input:focus {
	border: none;
	padding: 0;
	margin: 0;
}

.description {
	padding: 10px 0;
	font-family: Josefin Sans;
}

@media only screen and (max-width: 700px) {
	div.buttonList {
		grid-template-columns: unset;
	}
}

.modeSwitcher {
	grid-area: modes;
    display: grid;
	grid-auto-columns: 1fr;
	grid-auto-rows: 1fr;
    grid-auto-flow: column;
    overflow: hidden;
	column-gap: 15px;
	row-gap: 20px;
	font-size: 1em;
}

@media only screen and (max-width: 700px) {
	.modeSwitcher {
		grid-auto-flow: row;
	}
}

.modeSwitcher > a:first-child {
	overflow: hidden;
}

.modeSwitcher > a {
	border: none;
	border-radius: 10px;
	overflow: hidden;
}

.modeSwitcher > a > div {
	margin: 0px;
	grid-template-rows: unset;
	padding: 7px 0;
	display: flex;
}