/* Draggable */

.draggable {
	position: fixed;
	width: 31%;
	bottom: 10px;
	right: 10px;
	animation: move-to-corner 0.25s ease;
	display: flex;
	flex-direction: column;
}

.draggable div {
	width: 100%;
}

@keyframes move-to-corner {
	from {
		bottom: 20vh;
		right: 50vw;
	}
	to {
		bottom: 10px;
		right: 10px;
	}
}

/* Lightbox */


.lightBox {
	background-color: rgba(25, 25, 25, 0.68);
	position: fixed;
	width: 100vw;
	height: 90vh;
	bottom: 0px;
	left: 0px;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	font-family: 'Josefin Sans', sans-serif;
	overflow: hidden;
}

.lightBox .section {
	max-width: 60vw;
	background-color: lightgray;
	border: 1px rgba(128, 128, 128, 0.5) solid;
	border-radius: 40px;
	overflow: auto;
}
@media only screen and (max-width: 700px) {
	.lightBox .section {
		max-width: unset;
		max-height: 60vh;
	}
}
.lightBoxContent {
	box-sizing: border-box;
	padding: 25px;
}

.lightBoxTitle {
	display: flex;
	justify-content: center;
	font-weight: 600;
	background-color: orange;
	padding: 5px;
	cursor: pointer;
}

.lightBoxTitle svg {
	height: 100%;
}

div.lightBoxTitle:hover {
	background-color: indianred;
}

.lightBoxTitle > * {
	padding: 5px;
}

.dateTime {
	background-color: #f0f0f0;
	width: 100%;
	justify-content: center;
	align-items: center;
	padding: 5px;
	display: flex;
}