.canvas {
	width: 100%;
	height: 100%;
}

div.column {
	font-family: 'Josefin Sans', sans-serif;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: stretch;
	align-items: stretch;
	align-content: stretch;
	margin: auto;
	min-width: 500px;
	min-height: 400px;
	padding: 0px;
	background-color: rgba(200, 200, 200, 0.3);
}
.column:hover, .foreground:hover {
	top: 0px;
}


div.container {
	display: flex;
	flex-direction: column;
	justify-content: center;
}
.frame {
	display: flex;
	border-radius: 15px;
	min-height: 400px;
	align-items: stretch;
}

.loginSection {
	position: relative;
	border-radius: 15px;
	height: 100%;
	min-height: 400px;
	align-self: stretch;
}

div.initialiseButton {
	display: grid;
	height: 100%;
	width: 100%;
	overflow: hidden;
}

div.forgottenContainer {
	display: grid;
	grid-template-columns: repeat(2, 1fr);
	box-sizing: border-box;
	height: 100%;
	animation: fade-in 0.68s ease both;
}

div.forgotten {
	background-color: transparent;
	color: grey;
	display: flex;
	justify-content: center;
	align-items: center;
	box-sizing: border-box;
	height: 100%;
	font-size: 16px;
}

div.forgotten:first-child {
	border-right: 1px solid grey;
}

.form {
	display: grid;
	grid-template-rows: repeat(4, 1fr);
	height: 100%;
	width: 100%;
}

.form > .input {
	font-family: 'Josefin Sans', sans-serif;
	height: 100%;
	width: 100%;
	overflow: hidden;
	font-size: 16px;
	padding: 0px;
	margin: 0px;
	box-sizing: border-box;
	padding: 10px;
	border: none;
}
.input:first-child, .input:nth-child(2) {
	border-top: 1px solid grey;
}
.input:nth-child(2) {
	border-bottom: 1px solid grey;
}
.input:nth-child(3) {	
	animation: fade-in 0.68s ease both;
}

.foreground input.submit {
	font-family: 'Josefin Sans', sans-serif;
	height: 100%;
	width: 100%;
	justify-content: center;
	padding: 10px;
	margin: 0px;
	box-sizing: border-box;
	border: none;
	border-radius: 15px;
	color: white;
	background-color: #1e1e1e;
	-webkit-transition: all 0.25s ease-out;
	-moz-transition: all 0.25s ease-out;
	 -ms-transition: all 0.25s ease-out;
	  -o-transition: all 0.25s ease-out;
		 transition: all 0.25s ease-out;
}
.foreground input.submit:hover {
	background-color: #43853d;
}

.loginBackground {
	display: grid;
	grid-template-columns: 10ch 1fr 10ch;
	grid-template-rows: 100%;
	align-items: center;
	background-color: transparent;
}

.foreground {
	display: flex;
	flex-direction: column;
	align-items: center;
	position: absolute;
	height: calc(100% - 20px);
	width: calc(100% - 10ch - 20px);
	opacity: 1;
	margin: 0px;
	background-color: lightgrey;
}

.foreground input {
	background-color: transparent;
}
