.loginForeground {
	overflow: hidden;
	left: 0px;
	-webkit-transition: all 0.68s ease-out;
	-moz-transition: all 0.68s ease-out;
	 -ms-transition: all 0.68s ease-out;
	  -o-transition: all 0.68s ease-out;
		 transition: all 0.68s ease-out;
}

.registerForeground {
	left: 10ch;
	-webkit-transition: all 0.68s ease-out;
	-moz-transition: all 0.68s ease-out;
	 -ms-transition: all 0.68s ease-out;
	  -o-transition: all 0.68s ease-out;
		 transition: all 0.68s ease-out;
}

div.flip {
	display: flex;
	flex-direction: column;
	border-radius: 0px;
	align-items: center;
	cursor: pointer;
}

.flip > div {
    margin: 2px 0;
}

div.messageBar {
	display: flex;
	width: 100%;
    padding: 5px 0 5px 5px;
    box-sizing: border-box;
    border: 2px #252526 solid 0.0;
	box-shadow: none;
	justify-content: center;
	color: red;
}

@keyframes fade-in {
	from {
		opacity: 0.0;
	}
	to {
		opacity: 1.0;
	}
}

/* CROWN */


div.logo {
	width: 50%;
	max-width: 200px;
	display: grid;
	position: relative;
	grid-template: 100% / 100%;
}

img.staticLogo {
	width: 100%;
	position: relative;
	grid-column: 1 / span 1;
	grid-row: 1 / span 1;
}

img.loadingGif {
    position: absolute;
    width: 66.6%;
    max-width: 200px;
    transform: rotate(-33deg);
    opacity: 1;
    left: 6%;
    top: 15%;
	position: relative;
	grid-column: 1 / span 1;
	grid-row: 1 / span 1;
}

img.crownOutline {
    position: absolute;
    width: 71%;
    max-width: 213px;
    transform: rotate(-32deg);
    opacity: 1;
    z-index: 1;
    left: 5%;
    top: 13%;
	position: relative;
	grid-column: 1 / span 1;
	grid-row: 1 / span 1;
}

@media screen {
	.logo {
		animation: fade-in 1 0.5s linear;
	};
	.loadingLine {
		animation: grow-right infinite 0.68s linear;
	}
}

@keyframes fade-in {
	from {
		opacity: 0
	}
	to {
		opacity: 1
	}
}