@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, Roboto, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
    box-sizing: border-box;
    font: 400 20px/1.5 'Roboto', "Source Sans Pro", "Open Sans", "San Francisco", Helvetica, Arial, sans-serif;
    color: #333;
    margin: 0;
	background-color: #fff;
	overflow-y: scroll;
}

#root {
	display: flex;
	justify-content: center;
}

header,
#main,
footer {
    display: flex
}

section {
	width: 100%;
	margin-top: 25px;
}

.icon {
    height: 40px;
}

#main {
	flex: 1 0 auto;
	margin-top: 8vh;
	flex-direction: column;
	padding: 0px 20px;
	max-width: min(100%, 1080px);
	min-height: 68.1vh;
	box-sizing: border-box;
}

#main > section:last-of-type {
	margin-bottom: 80px;
}

.container {
	margin: 10px;
	background-color: rgba(200, 200, 200, 0.5);
	border-radius: 15px;
	padding: 10px;
	box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 10px 0px;
	font-family: 'Josefin Sans', sans-serif;
	position: relative;
}

.bodyContainer {
	border-radius: 25px;
}

.container:hover {
	top: -1px;
}

.button-list {
	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-flow: column;
	grid-column-gap: 10px;
	-webkit-column-gap: 10px;
	        column-gap: 10px;
	grid-row-gap: 10px;
	row-gap: 10px;
	overflow: hidden;
	border-radius: 25px;
}

.header-button, .header-button:link, .header-button:active {
	display: flex;
	background-color: skyblue;
	justify-content: center;
	text-align: center;
	box-sizing: border-box;
	cursor: pointer;
}

.header-button:hover > *{
	background-color: #43853d;
	color: white;
}

a.header-button.selected {
	background-color: cyan;
}

a.twitch-link.header-button:hover > *, .button-list > a.youtube-link.header-button:hover > * {
	-webkit-animation: flash-yellow 0.5s linear infinite alternate;
	        animation: flash-yellow 0.5s linear infinite alternate;
}

@-webkit-keyframes flash-yellow {
	from {
		background-color: #43853d;
	}
	to {
		background-color: transparent;
	}
}

@keyframes flash-yellow {
	from {
		background-color: #43853d;
	}
	to {
		background-color: transparent;
	}
}

a.header-button > div > * {
	padding: 0 20px;
}
a.header-button > div > svg, a.header-button > div > img {
	padding: 0 5px;
	height: 1em;
}


a.header-button.stream-link {
	display: flex;
}

a.header-button.stream-link > div {
	flex-direction: row;
	align-items: center;
}

a.header-button.twitch-link {
	background-color: #6441a5;
	color: rgb(80, 0, 80);
}

a.header-button.youtube-link {
	background-color: lightgrey
}

@media only screen and (max-width: 700px) {
	a.header-button.stream-link {
		grid-column: unset;
	}
}

.header-button div {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.header-button > div.full-text {
	grid-column: 1 / span 2;
	display: grid;
	grid-template-rows: 68% 32%;
	width: 100%;
	height: unset;
	position: relative;
	padding: 10px 0;
}

.header-button > div.full-text > * {
	margin: 2px 0;
}

.header-button > div.full-text > *:first-child:last-child {
	grid-row: 1 / span 2;
}

.header-button > div.full-text > h5 {
	font-size: 14px;
}

a.header-button img {
	max-height: 40px;
}

.button {
    display: flex;
    background-color: rgb(123, 121, 255);
    border-radius: 4px;
    width: 100%;
    height: 100%;
    align-items: center;
    align-content: center;
    justify-content: center;
	line-height: 20px;
	color: rgb(51, 51, 51);
	-moz-user-select: none;
	 -ms-user-select: none;
	     user-select: none;
	-webkit-user-select: none;
	font-weight: bold;
	cursor: pointer;
}

.button:link, a.button:visited {
	color: rgb(51, 51, 51);
}

.button:hover {
    color: #fff;
    background-color: #43853d
}

.button > svg {
    transition: transform 0.2s ease;
}

.button > * {
	padding: 10px 5px;
    height: 20px;
}

.iframe {
    width: 100%;
    min-height: 600px;
}

/* General */

#root {
	width: 100%;
	justify-content: center;
}

h1,
h2,
h3,
h4,
h5 {
