.bg-white {
    background-color: #fff !important
}

.bg-node-gray {
    background-color: #333 !important
}

.table-no-border-no-padding {
    border-spacing: 0
}

.table-no-border-no-padding td {
    padding: 0
}

.sr-only {
    position: absolute;
    width: 1px;
    height: 1px;
    padding: 0;
    overflow: hidden;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: none
}

.hidden {
    display: none
}

header,
footer {
    flex-shrink: 0
}

footer {
    padding: 0 20px
}

.row {
    display: flex;
    flex-direction: row
}

.list-divider-pipe {
    margin: 0;
    padding: 0
}

.list-divider-pipe li {
    display: inline-block
}

.list-divider-pipe li+li::before {
    content: "|";
