    color: #999
}

@media screen and (max-width: 700px ) {
    nav .list-divider-pipe {
        margin-bottom: 1rem;
        overflow: hidden
    }
	.button-list{
		grid-auto-flow: row;
	}
    nav .list-divider-pipe li+li::before {
