html {
    scroll-behavior: smooth
}

@media(prefers-reduced-motion: reduce) {
    html {
        scroll-behavior: auto
    }
}

#scroll-to-top {
    font-size: 1rem;
    color: #333;
    background-color: #f0f0f0;
    border: 1px solid #ccc;
    border-radius: 2px;
    position: fixed;
    bottom: 10%;
    right: 0;
    min-width: 20px;
    text-align: center;
    padding: 0 5px 1px;
    opacity: 0;
    -webkit-animation: buttonFade .5s 1s 1 forwards;
    animation: buttonFade .5s 1s 1 forwards
}

@-webkit-keyframes buttonFade {
    0% {
        opacity: 0
    }

    100% {
        opacity: 1
    }
}

@keyframes buttonFade {
    0% {
        opacity: 0
    }

    100% {
        opacity: 1
    }
}

@media(max-width: 600px) {
    #scroll-to-top span {
        display: none
    }
}

.anchor {
