.header {
    top: 0px;

    width: 100%;
    height: 8vh;
    z-index: 3; /* On account on react-carousel */
	background-color: skyblue;
	border-bottom: 2px darkgrey solid;

	display: grid;
	position: fixed;
    grid-template-columns: 20vh 1fr;
    align-items: start;
    -webkit-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.2);
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.2);
    font-family: 'Josefin Sans', 'Helvetica Neue', sans-serif;
}

.buttonContainer {
    display: grid;
    grid-template-columns: auto repeat(3, 1fr);
    grid-auto-flow: column;
	grid-area: 'name link link link link';
	align-items: center;
	height: inherit;
}

.buttonContainer .button:first-child {
	padding: 0 40px;
}

@media only screen and (max-width: 700px) {
	.header {
		grid-template-columns: 100%;
	}
	.buttonContainer, div.header .nameWrapper {
		display: none;
	}
}

.logoWrapper, .logoWrapper > img {
	z-index: 1;
}

a.button {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
	background-color: transparent;
	font-size: 1.6vw;
	padding: 0 20px;
	height: inherit;
	box-sizing: border-box;
}

a.button:hover {
	border-bottom: skyblue 5px solid;
}

@media only screen and (max-width: 700px) {
    div.installer {
        flex-direction: column;
    }
    div.icons {
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-auto-flow: unset;
    }
    .header > .logoWrapper {
		display: flex;
		height: 20vh;
		justify-content: center;
		background-color: transparent;
	}
	.logoWrapper > img {
		height: 100%;
	}
}